<template>
    <div id="layoutTest">
        <layout1>
            <template slot="layout_l">
                <tree></tree>
            </template>
            <template slot="layout_r">
                <div>
                    <h1>右边</h1>
                </div>
            </template>
            <template slot="layout_rf">
                <div>
                    <p>右下</p>
                </div>
            </template>
        </layout1>
    </div>
</template>

<script>
import Layout1 from "@/components/common/layout/layout1";
import tree from "@/components/common/tree";
export default {
  components: {
    Layout1,
    tree
  },

  data() {
    return {};
  },
  mounted() {},
  methods: {
    
  }
};
</script>

<style lang="scss" scoped>
#layoutTest {
  height: 100%;
  background: red;
}
</style>





